.container {
  padding: 2.25rem 0.75rem 0.625rem 0.75rem;
  background: #0085ff;
}
.form {
  min-width: 10rem;
  margin: 0 auto;
}
.form p {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 0.5rem 0 0.875rem;
  margin: 0;
  background: #fff;
  border-radius: 10px;
}
.form input {
  width: 100%;
  border: none;
  padding: 0.625rem 0;
  border-radius: 5px;
  font-size: 0.875rem;
  font-style: inherit;
  background: transparent;
}

.form input:focus {
  outline: none;
}

.form button {
  width: 2rem;
  height: 2rem;
  border: none;
  background: url(../assets/images/search.png) center no-repeat;
  background-size: 1rem;
  cursor: pointer;
}
