.container {
  padding: 1rem 1.125rem;
  margin-bottom: 1rem;
  background: #0085ff;
  border-radius: 10px;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}
.flex_end {
  justify-content: flex-end;
}
.layout {
  text-align: left;
  cursor: pointer;
}
.list {
  display: flex;
  justify-content: space-between;
}
.list h3 {
  font-size: 1.125rem;
  line-height: 22px;
  color: #fff;
  word-break: keep-all;
}
.list_info {
  padding: 0.75rem 0;
  font-size: 0.875rem;
  color: #fff;
}
.list_category {
  display: inline-block;
  padding: 0.3125rem 0.625rem;
  border-radius: 20px;
  background: #fff;
  font-size: 0.75rem;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.25);
}
.btn {
  border: none;
  background: none;
  cursor: pointer;
  height: 20px;
}

.btn img {
  width: 20px;
  height: 20px;
}
.actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.actions > div {
  display: flex;
  align-items: center;
}
.actions > div > img {
  width: 1.25rem;
  height: 1.125rem;
  margin-right: 0.325rem;
}
.actions > div > p {
  color: #fff;
}
